@import "../../helpers/styles.scss";

button.button {
    background-color: #ffa841;
    color: white;
    font-weight: 500;
    font-size: 0.9rem;
    border-radius: 4px;
    padding: 5px 10px !important;
    min-height: 30px;
    outline-width: 0px;
    border: none;
    cursor: pointer;
    white-space: nowrap;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: capitalize;
    @include Responsive("mobile") {
        padding: 2px 10px !important;
        font-size: 14px !important;
    }
    &:hover,
    &:focus {
        transform: scale(1.02);
        outline-width: 0px;
    }
    &:disabled {
        opacity: 0.8;
        pointer-events: none;
    }
}
